import React, { ReactNode } from "react";
import styled from "styled-components";

import { Row } from "../Grid";
import { colors, spacingMap } from "../styleguide";

type DrawerFooterProps = {
	leftActions?: ReactNode;
	rightActions?: ReactNode;
	className?: string;
};

const StyledDrawerFooter = styled.div<DrawerFooterProps>`
	display: flex;
	flex-direction: row;
	// if there are no leftActions, place the right actions to the right
	justify-content: ${({ leftActions }) => leftActions ? "space-between" : "flex-end"};
	margin-top: auto;
	padding: ${spacingMap.sm};
	border-top: 1px solid ${colors.lightGray};
`;

const StyledRow = styled(Row)`
	gap: ${spacingMap.xs}
`;

const DrawerFooter = ({
	leftActions,
	rightActions,
	className,
}: DrawerFooterProps) => (
	<StyledDrawerFooter
		className={className}
		leftActions={leftActions}
	>
		{leftActions && (
			<StyledRow>
				{leftActions}
			</StyledRow>
		)
		}
		{rightActions && (
			<StyledRow>
				{rightActions}
			</StyledRow>
		)
		}
	</StyledDrawerFooter>
);

export default DrawerFooter;
